.logo {
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  font-size: 0;
  position: relative;
  width: 240px;
  padding: 0;
  text-align: left;
  color: #333;
  line-height: 24px;
  height: 24px;
  white-space: nowrap; }
  .logo__word {
    display: inline-block;
    font-size: 24px; }
  .logo__dot {
    color: #D64933;
    font-size: 48px;
    line-height: 0; }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 62px;
  border-bottom: 4px solid #c3cbcc; }
  .header__logo {
    padding-left: 12px;
    padding-left: 12px; }

.menu {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  height: 62px;
  line-height: 62px; }
  .menu__item {
    margin-right: 12px;
    text-decoration: none;
    color: #333;
    position: relative;
    white-space: nowrap; }
    .menu__item:last-of-type {
      margin-right: 0; }
    .menu__item:hover:after {
      content: '';
      bottom: -4px;
      height: 4px;
      position: absolute;
      left: 0;
      background-color: #D64933;
      width: 100%;
      opacity: 0.4; }
    .menu__item.is-active {
      font-weight: 600; }
      .menu__item.is-active:after {
        content: '';
        bottom: -4px;
        height: 4px;
        position: absolute;
        left: 0;
        background-color: #D64933;
        width: 100%; }
      .menu__item.is-active:hover:after {
        opacity: 1; }
  .menu__link {
    display: block;
    line-height: 62px;
    height: 62px;
    text-decoration: none;
    color: #333;
    padding-right: 6px;
    padding-left: 6px; }
  .menu__ic-home {
    display: block;
    width: 24px;
    height: 24px;
    background: url("../images/ic_home.svg") no-repeat center;
    background-size: contain; }

